.App {
  text-align: center;
}

body {
  background: #333;
  color: hsl(200, 100%, 90%);
  max-width: 400px;
  padding: 1rem;
  margin: 0 auto;
}

.new-item-form {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-row {
  display: flex;
  flex-direction: column;
  gap: 0.1rem;
}

btn:hover,
.btn:focus-visible {
  background: hsl(200, 100%, 50%, 0.2);
}


.new-item-form input {
  outline: none;
  border: 1px solid hsl(200, 100%, 40%);
  background: hsl(200, 100%, 30%);
  border-radius: 0.25em;
  padding: 0.25em 0.5em;
  color: hsl(200, 100%, 90%);
}


.header {
  font-size: 2rem;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}


.list {
  margin: 0;
  padding: 0;
  margin-left: 1rem;
  list-style: none;
}